nav {
  padding: 0 1rem;
}

.content {
  min-height: calc(100vh - 82px - 64px);
  padding: 1.5rem 0;
}
.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 1rem;
}
.search-btn {
  position: absolute;
  top: 0;
  right: 0.5rem;
}
label:not(:last-child) {
	margin-right: 3rem;
}
.main {
	background-color: #FFE0B2
;
}